<template>
  <Loading :active="isLoading"></Loading>
  <div class="container-fluid">
    <div class="d-flex justify-content-between align-items-start">
      <div>
        <SearchData ref="searchDataRef" @search="searchData" :value="searchCondition"></SearchData>
      </div>
      <div class="d-flex justify-content-between align-items-start">
            <button type="button" class="btn btn-outline-secondary" @click="eraseSearch"></button>  
          </div>
      <div>

        <div class="d-flex align-items-center">
          
          <span></span>
          <select class="form-select" v-model="searchCondition" style="width:300px" :key="searchCondition">
            <option value="ID">ID</option>
            <option value="building_name"></option>
            <option value="applicane"></option>
            <option value="designer"></option>
          </select>
        </div>
      </div>
      <div>
        <button type="button" class="btn btn-primary" @click.prevent="openModal(true, tempDataInfo)"></button>
      </div>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120">ID</th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="200"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in filteredItems" :key="item.ID">
          <td>{{ item.ID }}</td>
          <td>{{ item.building_name }}</td>
          <td>{{ item.applicane }}</td>
          <td>{{ item.designer }}</td>
          <td>{{ item.building_summary }}</td>
          <td>
            <div class="btn-group">
              <ExportWordButton @click="getAllData(item)" :item="building_AllData" />
              &nbsp;&nbsp;&nbsp;&nbsp;
            </div>
            <button type="button" class="btn btn-outline-success" @click="openModal(false, item)"></button>
            &nbsp;&nbsp;
            <button type="button" class="btn btn-outline-primary" @click="openXmlBuilderModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination :current-page="currentPage" :total-pages="totalPages" @update-page="updatePage" />
    <BuildingMessageModal ref="buildingMessageModal" :data="tempDataInfo" @update-data-info="updateDataInfo"
      :is-empty-cert-number="isNew">
    </BuildingMessageModal>
   
    <XmlBuilderModal ref="xmlBuilderModal" :data="tempDataInfo"></XmlBuilderModal>
    
  </div>
</template>

<script>
import BuildingMessageModal from '@/components/BuildingMessageModal.vue';
import DelModal from '@/components/DelModal.vue';
import Pagination from '@/components/Pagination.vue';
import ExportWordButton from '@/components/ExportWordButton.vue';
import SearchData from '@/components/SearchData.vue'
import XmlBuilderModal from '@/components/XmlBuilderModal.vue';
import CloseModal from '@/components/CloseModal.vue';
export default {
  data() {
    return {
      searchCondition : 'ID',
      currentPage: 1,
      totalPages: 0,
      pageSize: 20,
      isSearch : true,
      building_data: [],
      table: '',
      pagination: {},
      tempDataInfo: {},
      isNew: false,
      isLoading: false,
      isCIB: false,
      building_AllData: [],
      searchQuery: '1',
    };
  },
  components: {
    ExportWordButton,
    BuildingMessageModal,
    DelModal,
    Pagination,
    SearchData,
    XmlBuilderModal,
    CloseModal
  },
  inject: ['emitter'],
  computed: {
    filteredItems() {
      const filteredData = this.building_data.filter(item => {
        const searchProperty = this.searchCondition;    
          return searchProperty ? item[searchProperty].includes(this.searchQuery.trim()) : true;      
      });
      
      this.totalPages = Math.ceil(filteredData.length / this.pageSize);
      console.log(this.totalPages)
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return filteredData.slice(start, end);
    },
  },
  methods: {
    eraseSearch(){
      const searchDataComponent = this.$refs.searchDataRef;
      searchDataComponent.clearInput();
      this.searchQuery = ''
    },    
    openXmlBuilderModal(item){
      this.getAllData(item)
          .then(() => {
            console.log(this.building_AllData);
            this.tempDataInfo = {
              ...this.building_AllData[0],
            }
          })
          .catch(error => {
            console.error('Failed to fetch data:', error);
       });
      const xmlBuilderModalComponent = this.$refs.xmlBuilderModal;
      xmlBuilderModalComponent.showModal();
    },
    computeTotalPages() {
      this.totalPages = Math.ceil(this.filteredItems.length / this.pageSize);
    },
   searchData(searchQuery, searchCondition) {
      this.getData()
      this.searchQuery = searchQuery;
      this.currentPage = 1;
      this.computeTotalPages();
      this.updatePage(this.currentPage);
      this.isSearch = true
      
    },
    openCloseModal() {
      const closeModalComponent = this.$refs.closeModal
      closeModalComponent.showModal();
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempDataInfo = {};
      } else {
        this.getAllData(item)
          .then(() => {
            this.tempDataInfo = {
              ...this.building_AllData[0],
            }
            for (const prop in this.tempDataInfo) {
              console.log(this.tempDataInfo[prop] === '-')
              if (this.tempDataInfo[prop] === '-' || this.tempDataInfo[prop]==='') {
                this.tempDataInfo[prop] = '';
                console.log(this.building_AllData);
              }
            }
          })
          .catch(error => {
            console.error('Failed to fetch data:', error);
          });
      }
      console.log(isNew)
      this.isNew = isNew
      const isEmptyCertNumber = isNew; 
      const buildingMessageComponent = this.$refs.buildingMessageModal;
      buildingMessageComponent.showModal(isEmptyCertNumber);
    },
    getData() {
      const url = `${process.env.VUE_APP_API}/user`;
      const params = {
        table: this.table,
      };
      this.isLoading = true
      this.$http
        .get(url, { params })
        .then(res => {
          if (Array.isArray(res.data) && res.data.length > 0) {
            this.isLoading = false
            this.building_data = res.data;
            this.totalPages = Math.ceil(this.building_data.length / this.pageSize);
          } else {

            console.error('Invalid data structure:', res.data);
          }
        })
        .catch(error => {
          console.error('Failed to fetch data:', error);
          if (this.isLoading === true) {
            console.log("error")
            this.emitter.emit('push-message', {
              style: 'danger',
              title: '',
              content: 'server',
            });
            this.isLoading = false
          }
        });
    },
    updatePage(page) {
      
      this.currentPage = page;

    },
    updateDataInfo(item) {
      const params = { ...item };
      console.log(params);
      console.log(this.isNew === true)
      if (this.isNew === true) { // 
        this.tempDataInfo = item;
        let url = `${process.env.VUE_APP_API}/insert/buildingMessage`;
        const params = { ...item };
        this.$http
          .get(url, { params })
          .then(res => {
            console.log('success insert building message');
          })
          .catch(error => {
            console.error('Failed to insert buildingMessage:', error);
          });
        if (item.certNumber.includes('CIB')) {
          url = `${process.env.VUE_APP_API}/insert/candidateMessage`;
        } else {
          url = `${process.env.VUE_APP_API}/insert/labelMessage`;
        }
        this.$http
          .get(url, { params })
          .then(res => {
            // console.log('success get candidate_message/label_message');
            this.emitter.emit('push-message', {
              style: 'success',
              title: '',
              content: '',
            });
          })
          .catch(error => {
            // console.error('Failed to update candidate_message/label_message:', error);
          });
          url = `${process.env.VUE_APP_API}/insert/xmlBuilding_Info`
          this.$http
          .get(url, { params })
          .then(res => {
            console.log('success insert_xmlBuilding_Info');
          })
          .catch(error => {
            console.error('Failed to insert_xmlBuilding_Info:', error);
          });
      } else {  //
        this.tempDataInfo = item;
        
        let url = `${process.env.VUE_APP_API}/update/buildingMessage`;
        this.$http
          .get(url, { params })
          .then(res => {
            console.log('success update buildingMessage');
          })
          .catch(error => {
            console.error('Failed to update_buildingMessage:', error);
          });
        if (item.certNumber.includes('CIB')) {
          url = `${process.env.VUE_APP_API}/update/candidateMessage`;
        } else {
          url = `${process.env.VUE_APP_API}/update/labelMessage`;
        }
        this.$http
          .get(url, { params })
          .then(res => {
            this.emitter.emit('push-message', {
              style: 'success',
              title: '',
              content: '',
            });
          })
          .catch(error => {
            console.error('Failed to update candidate_message/label_message:', error);
          });
          
          console.log(this.tempDataInfo)
          console.log(this.item)
          console.log('update')
          url = `${process.env.VUE_APP_API}/update/xmlBuilding_Info`
          this.$http
          .get(url, { params })
          .then(res => {
            console.log(res.data)
            console.log('success update xmlBuilding_Info');
          })
          .catch(error => {
            console.error('Failed to update xmlBuilding_Info', error);
          });
        }
        
        this.getData()
        console.log(this.tempDataInfo.certNumber)
        this.isSearch = false
        this.searchQuery = this.tempDataInfo.certNumber
    },
    getAllData(item) {
      const url = `${process.env.VUE_APP_API}/select/getAllData`;

      const params = {
        ID: item.ID
      };
      return this.$http
        .get(url, { params })
        .then(res => {
          if (Array.isArray(res.data) && res.data.length > 0) {
            this.building_AllData = res.data;
          } else {
            console.error('Invalid data structure:', res.data);
          }
        });
    }
  },
  created() {
    this.table = 'building_message';
    this.computeTotalPages();
    this.getData();
  }
};
</script>
