<template>
  <!-- Modal -->
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true"
    ref="modal">
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span></span>
          </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row gx-3">
            <div class="mb-3 col-md-4">
              <label for="certNumber" class="form-label">()</label>
              <input type="text" class="form-control" id="certNumber" placeholder=""
                v-model="tempDataInfo.certNumber" @blur="checkDuplicateCertNumber"
                :class="{ 'input-error': errors.certNumber, 'input-success': !errors.certNumber }"
                :disabled="!isEmptyCertNumber" />
              <br>
              <span id="error" v-if="errors.certNumber">{{ errors.certNumber }}</span>
              <span id="success" v-if="success.certNumber">{{ success.certNumber }}</span>
            </div>
            <div class="mb-3 col-md-4">
              <label for="assessmentNumber" class="form-label"></label>
              <input type="text" class="form-control" id="assessmentNumber" placeholder=""
                v-model="tempDataInfo.assessmentNumber">
            </div>
            <div class="mb-3 col-md-4">
              <label for="publicOrPrivate" class="form-label"> / </label>
              <select type="text" class="form-control" :value=value id="publicOrPrivate" placeholder=" / "
                v-model="tempDataInfo.publicOrPrivate">
                <option value="0"></option>
                <option value="1"></option>
              </select>
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-1 col-md-4">
              <label for="buildingName" class="form-label"></label>
              <input type="text" class="form-control" id="buildingName" placeholder=""
                v-model="tempDataInfo.buildingName">
            </div>
            <div class="mb-1 col-md-4">
              <label for="designer" class="form-label"></label>
              <input type="text" class="form-control" id="designer" placeholder=""
                v-model="tempDataInfo.designer">
            </div>
            <div class="mb-1 col-md-4">
              <label for="builder" class="form-label"></label>
              <input type="text" class="form-control" id="builder" placeholder="" v-model="tempDataInfo.owner">
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-3 col-md-4">
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-3 col-md-4">
              <label for="usageType" class="form-label"></label>
              <input type="text" class="form-control" id="usageType" placeholder=""
                v-model="tempDataInfo.usageType">
            </div>
            <div class="mb-3 col-md-4">
              <label for="applicableStandard" class="form-label"></label>
              <select type="text" class="form-select" aria-label="" id="applicableStandard" placeholder=""
                v-model="tempDataInfo.applicableStandard">
                <option value="2011">2011</option>
                <option value="2016">2016</option>
              </select>

            </div>
            <div class="mb-3 col-md-4">
              <label for="level" class="form-label"></label>
              <select type="text" min="0" max="4" class="form-select" aria-label="" id="level" placeholder=""
                v-model="tempDataInfo.level">
                <option value="-1"></option>
                <option value="0"></option>
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
                <option value="4"></option>
              </select>
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-3 col-md-4">
              <label for="passCriteria" class="form-label"></label>
              <input type="text" class="form-control" id="passCriteria" placeholder=""
                v-model="tempDataInfo.passCriteria">
            </div>
            <div class="mb-3 col-md-4">
              <label for="indicatorCount" class="form-label"></label>
              <input type="text" class="form-control" id="indicatorCount" placeholder=""
                v-model="tempDataInfo.indicatorCount">
            </div>
            <div class="mb-3 col-md-4">
              <label for="location" class="form-label"></label>
              <input type="text" class="form-control" id="location" placeholder="" v-model="tempDataInfo.location">
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-3 col-md-4">
              <label for="totalFloorArea" class="form-label"> (m2)</label>
              <input type="text" class="form-control" id="totalFloorArea" placeholder=""
                v-model="tempDataInfo.totalFloorArea">
            </div>
            <div class="mb-3 col-md-4">
              <label for="constructionCost" class="form-label">()</label>
              <input type="text" class="form-control" id="constructionCost" placeholder=""
                v-model="tempDataInfo.constructionCost">
            </div>
            <div class="mb-3 col-md-4">
              <label for="buildingSummary" class="form-label"></label>
              <input type="text" class="form-control" id="buildingSummary" placeholder=""
                v-model="tempDataInfo.buildingSummary">
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-1 col-md-4">
              <label for="unitCount" class="form-label"></label>
              <input type="text" class="form-control" id="unitCount" placeholder="" v-model="tempDataInfo.unitCount">
            </div>
            <div class="mb-1 col-md-4">
              <label for="projectType" class="form-label"></label>
              <input type="text" class="form-control" id="projectType" placeholder=""
                v-model="tempDataInfo.projectType">
            </div>
            <div class="mb-1 col-md-4">
              <label for="contractor" class="form-label"></label>
              <input type="text" class="form-control" id="contractor" placeholder=""
                v-model="tempDataInfo.contractor">
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-1 col-md-4">
              <label for="regulatoryRequirements" class="form-label"></label>
              <input type="text" class="form-control" id="regulatoryRequirements" placeholder=""
                v-model="tempDataInfo.regulatoryRequirements">
            </div>
            <div class="mb-1 col-md-4">
              <label for="bonusName" class="form-label"></label>
              <input type="text" class="form-control" id="bonusName" placeholder=""
                v-model="tempDataInfo.bonusName">
            </div>
            <div class="mb-1 col-md-4">
              <label for="bonusVolume" class="form-label"></label>
              <input type="text" class="form-control" id="bonusVolume" placeholder=""
                v-model="tempDataInfo.bonusVolume">
            </div>
          </div>
          <div class="row gx-3">
            <div class="mb-1 col-md-4" v-if=!isCIB>
              <label for="candidateCertNumber" class="form-label"></label>
              <input type="text" class="form-control" id="candidateCertNumber" placeholder=""
                v-model="tempDataInfo.candidateCertNumber">
            </div>
            <div class="mb-1 col-md-4">
              <label for="administrativeDistrict" class="form-label">(xml)</label>
              <input type="text" class="form-control" id="administrativeDistrict" placeholder=""
                v-model="tempDataInfo.administrativeDistrict">
            </div>
            <div class="mb-1 col-md-4">
              <label for="ownerAddress" class="form-label">(XML)</label>
              <VueTwZipCodeSelector @change=changecounty ref="ZipCode" v-model="ZipCode"/>

                       
              <input type="text" class="form-control" id="owner_address" placeholder=""
                v-model="tempDataInfo.ownerAddress">

            </div>
          </div>
          <div class="row gx-3">

            <div class="mb-1 col-md-4">
              <label for="designer_address" class="form-label"></label>
              <input type="text" class="form-control" id="designer_address" placeholder=""
                v-model="tempDataInfo.designer_address">
            </div>
            <div class="mb-1 col-md-4">
              <label for="plateNumber" class="form-label">(xml)</label>
              <input type="text" class="form-control" id="plateNumber" placeholder="" v-model="tempDataInfo.address">
            </div>
          </div>
          <hr>
          <div class="mb-3">
            <div class="form-check">
              <span v-if="errors.certNumber" class="error">{{ errors.certNumber }}</span>
            </div>
          </div>
          <div class="d-flex justify-content-between">
            <div>
              <button type="button" class="btn btn-primary me-2" @click="cleanData">
                
              </button>
              <button type="button" class="btn btn-primary me-2" @click="recoverData" :disabled="!canRecoverData">
                
              </button>
            </div>
            <div>
              <button type="button" class="btn btn-primary me-2" :disabled="!canCreated" @click="delay">
                
              </button>
              <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
.input-error {
  /*  */
  border: 2px solid red;
}

.input-success {
  /*  */
  border: 2px solid green;
}
</style>

<script>
import modalMixin from '@/mixins/modalMixin';

export default {
  mixins: [modalMixin],
  props: {
    data: Object,
    isEmptyCertNumber: Boolean,
    //  props ...
  },
  watch: {
    data() {
      if (this.data) {
        this.tempDataInfo = {}
        this.tempDataInfo = { ...this.data }
      }
      this.tempDataInfo = {
        certNumber: this.tempDataInfo.ID,
        assessmentNumber: this.tempDataInfo.Assessment_ID,
        buildingName: this.tempDataInfo.building_name,
        designer: this.tempDataInfo.designer,
        owner: this.tempDataInfo.applicane,
        usageType: this.tempDataInfo.class,
        publicOrPrivate: this.tempDataInfo.governmental,
        applicableStandard: this.tempDataInfo.version,
        level: this.tempDataInfo.LEVEL,
        passCriteria: this.tempDataInfo.project_name,
        indicatorCount: this.tempDataInfo.project_num,
        location: this.tempDataInfo.building_local,
        totalFloorArea: this.tempDataInfo.building_area,
        constructionCost: this.tempDataInfo.cost_of_building,
        buildingSummary: this.tempDataInfo.building_summary,
        unitCount: this.tempDataInfo.household_num,
        projectType: this.tempDataInfo.status,
        contractor: this.tempDataInfo.taker,
        regulatoryRequirements: this.tempDataInfo.regulations,
        bonusName: this.tempDataInfo.regulations_of_bulk_reward,
        bonusVolume: this.tempDataInfo.reward_range,
        candidateCertNumber: this.tempDataInfo.candidate_ID,
        administrativeDistrict: this.tempDataInfo.administrativeDistrict,
        designer_address: this.tempDataInfo.designer_address,
        ownerAddress: this.tempDataInfo.owner_address,
        address: this.tempDataInfo.address,
      };
    },
    'myZipcode':function(newVal,oldVal) {
      console.log(newVal,oldVal);
    },
    'tempDataInfo.publicOrPrivate': function (newVal, oldVal) {

      // 
    },
    'tempDataInfo.certNumber': function (newVal, oldVal) {
      if (this.tempDataInfo.certNumber) {
        if (this.tempDataInfo.certNumber.includes('CIB')) {
          this.isCIB = true
        } else {
          this.isCIB = false
        }
      }

      this.validateCertNumber();
    }
  },
  data() {
    return {
      myCounty: '',
      ZipCode: '',
      disabled: false,
      timeout: null,
      isNew: '',
      modal: {},
      tempDataInfo: { certNumber: "" },
      errors: {},
      success: {},
      canCreated: false,
      isUpdate: false,
      isCIB: false,
      eraseData: {}
    };
  },
  methods: {
    changecounty(){
      const countyValue = this.$refs.ZipCode;
      console.log('County Value:', countyValue); 
      
      console.log('County Value:', countyValue.selectedCounty.name);
      console.log('County Value:', countyValue.selectedZone.name);
      console.log('County Value:', countyValue.selectedZone.zipCode); 
      this.tempDataInfo.ownerAddress = countyValue.selectedZone.zipCode + countyValue.selectedCounty.name + countyValue.selectedZone.name
    }, 

    cleanData() {
      this.eraseData = { ...this.tempDataInfo };
      this.tempDataInfo = {};
    },
    recoverData() {
      this.tempDataInfo = { ...this.eraseData };
      this.eraseData = {};
    },
    checkEraseData() {
      return Object.keys(this.eraseData).length > 0;
    },
    async checkDuplicateCertNumber(certNumber) {
      try {
        const response = await this.$http.get(`${process.env.VUE_APP_API}/select/checkDuplicateCertNumber`, { params: { certNumber } });
        return response.data;
      } catch (error) {
        return false;
      }
    },
    async delay() {
      this.disabled = true;
      // Re-enable after 5 seconds
      this.timeout = setTimeout(() => {
        this.disabled = false;
      }, 5000);
      await this.myFunction();
      this.isUpdate = false
      this.hideModal()
    },
    async myFunction() {
      const isValid = await this.validateCertNumber();
      if (isValid) {
        this.$emit('update-data-info', this.tempDataInfo);
      }
    },
    async validateCertNumber() {
      const certNumber = this.tempDataInfo.certNumber;
      if (!certNumber) {
        this.errors.certNumber = '';
        this.success.certNumber = ''
        return false;
      }
      const isDuplicate = await this.checkDuplicateCertNumber(certNumber);
      const isValid = certNumber.includes('CIB') || certNumber.includes('IB')
      this.isNew = this.isEmptyCertNumber
      console.log(this.isNew)
      console.log(isValid)
      console.log(isDuplicate)
      if (isDuplicate && this.isNew) {
        this.errors.certNumber = '';
        this.canCreated = false
        this.success.certNumber = ''
        return false;
      }
      else if (isDuplicate && !this.isNew && isValid) {
        this.errors.certNumber = '';
        this.success.certNumber = '()'
        this.canCreated = true
        return true;
      }
      else {
        if (!isValid) {
          this.errors.certNumber = '';
          this.canCreated = false
          this.success.certNumber = ''
          return false;
        }
        this.errors.certNumber = ''
        this.success.certNumber = ''
        this.success.certNumber = '';
        this.canCreated = true
        return true;
      }
    },
  }, computed: {
    canRecoverData() {
      return Object.keys(this.eraseData).length > 0;
    },
  },
  created() {
    this.tempDataInfo = this.data


  },
  mounted() {
    //  eraseData 
    this.eraseData = this.checkEraseData();
  },
  unmounted() {
    this.success.certNumber = ''
    this.errors.certNumber = ''
  }
};
</script>