                "	<>   </>\n" +
                "	<></>\n" +
                "	<>040203</>\n" +
                "      <>03</>\n" +
                "	<>5</>";

            const xml_tail = "</>\n" +
                "	<>\n" +
                "		<></>\n" +
                "	</>\n" +
                "	</>\n" +
                "	<>\n" +
                "		<>1100901</>\n" +
                "	</>\n" +
                "	</>\n" +
                "	< =''/>\n" +
                "	< =''/>\n" +
                "	</>\n" +
                "	</>\n" +
                "	<>()</>\n" +
                "	<>()</>\n" +
                "	<></>\n" +
                "	<>\n" +
                "		<>N</>\n" +
                "	</>\n" +
                "</>";

            XML_total =
                xml_head +
                Subject_text +
                "	< =''>\n" +
                "		</>\n" +
                //
                description[1] +
                description[2] +
                description[3] +
                description[4] +
                description[5] +
                "	</>" +
                xml_tail;
            const saveToFile = (data, filename, foldername) => {
                const blob = new Blob([data]);

                if (window.navigator.msSaveOrOpenBlob) {
                    // IEmsSaveOrOpenBlob
                    window.navigator.msSaveOrOpenBlob(blob, `${foldername}/${filename}`);
                } else {
                    // FileSaver.js
                    const fileUrl = URL.createObjectURL(blob);
                    const link = document.createElement('a');
                    link.href = fileUrl;
                    link.download = filename;

                    document.body.appendChild(link);
                    link.click();
                    document.body.removeChild(link);

                    URL.revokeObjectURL(fileUrl);
                }
            };
            try {
                saveToFile(XML_total, `${certNumber}_${building_name}_.xml`);
            } catch (error) {
                console.error(error);
            }
        }


    }
