    <template>
      <div>
        <button class="btn btn-outline-info" @click="exportWord"> Word</button>
      </div>
    </template>

    <script>
    import { ref } from 'vue';
    import docxtemplater from 'docxtemplater';
    import PizZip from 'pizzip';
    import { saveAs } from 'file-saver';

    export default {
      props: {
        item: {
          type: Object,
          required: true
        },
      },
      methods: {
        exportData() {
          console.log('Exporting data:', this.buildingData);
          // 
        }
      },
      setup(props) {
        const exportWord = async () => {
          const videoParam = ref({
            data: [props.item]
          });
          console.log(props)
          try {
            const templateName = props.item[0].ID.includes('CIB') ? 'EX.docx' : 'EX1.docx';
            const content = await getFileContent(templateName);
            const zip = new PizZip(content);
            const doc = new docxtemplater().loadZip(zip);
            const currentYear = new Date().getFullYear();
            console.log(currentYear);
            const levelToString = (level) => {
              if(level< 1) return "";
              else if (level === 0) return "";
              else if (level === 1) return "";
              else if (level === 2) return "";
              else if (level === 3) return "";
              else if (level === 4) return "";
            };
            doc.setData({
              table: videoParam.value.data,
              certNumber : props.item[0].ID ,
              now_year : currentYear-1911, 
              now_year1 : currentYear-1911+5,
              building_summary : props.item[0].building_summary,
              building_name : props.item[0].building_name,
              LEVEL : levelToString(props.item[0].LEVEL),
              version : props.item[0].version,
  
              
            });

            doc.render();

            const out = doc.getZip().generate({
              type: "blob",
              mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document"
            });

            saveToFile(out, `${props.item[0].ID}_${props.item[0].building_name}.docx`);
          } catch (error) {
            console.error(error);
          }
        };

        const getFileContent = (url) => {
          return new Promise((resolve, reject) => {
            const xhr = new XMLHttpRequest();
            xhr.open('GET', url, true);
            xhr.responseType = 'arraybuffer';

            xhr.onload = function () {
              if (xhr.status === 200) {
                resolve(xhr.response);
              } else {
                reject(new Error(xhr.statusText));
              }
            };

            xhr.onerror = function () {
              reject(new Error(''));
            };

            xhr.send();
          });
        };
        const fileSaver = require('file-saver');
        const saveToFile = (data, filename) => {
          const blob = new Blob([data], { type: "application/vnd.openxmlformats-officedocument.wordprocessingml.document" });
          saveAs(blob, filename);
        };

        return {
          exportWord
        };
      },
      methods: {
            
        } 
    };
    </script>
